import React from "react";
import {
    Row,
    Col,
    Card,
    Button,
    InputGroup,
    FormControl,
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, i18n.t('error-short-governorate'))
        .max(20, i18n.t('error-long-governorate'))
        .required(i18n.t('error-required-governorate')),
    country: Yup.string()
        .min(2, "Country is Too Short!")
        .max(20, "Country is Too Long!")
        .required("Country is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
    gouvernorat: Yup.string()
        .required(i18n.t('error-address')),
});
class AddZone extends React.Component {
    state = {

        privileges: [],
        agences: [],
        showMessageParams: {
            display: false,
            type: "success",
            message: null,
        },
    };

    componentDidMount() {
        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios
            .get(urlbackend + "/agence", config)
            .then((response) => {
                console.log("this respon list", response.data);
                this.setState({ agences: response.data.data });
            })
            .catch((e) => {
                console.log(e);
            });

    }

    handlSubmit = (values) => {
        this.setState({
            showMessageParams: {
                display: false,
                type: "success",
                message: null

            }
        });
        console.log(values);

        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.post(urlbackend + "/addZone ", values, config)
            .then(response => {
                this.setState({
                    showMessageParams: {
                        display: true,
                        type: "success",
                        message: i18n.t('success-add-zone')
                    }
                });
                console.log(response.data)

            })
            .catch(e => {
                this.setState({
                    showMessageParams: {
                        display: true,
                        type: "danger",
                        message: i18n.t('error-add-zone')
                    }
                });
                console.log(e)
            })
    };
    showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        const { showMessageParams } = this.state;
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">{i18n.t('add-zone')}</Card.Title>
                            </Card.Header>
                            {showMessageParams.display == true ?
                                <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                                : null
                            }
                            <Card.Body>
                                <Row>
                                    <Col md={6}>





                                        {/* <CustomInput
                      name="password"
                      label="password"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.password}
                      errors={errors.password}
                      touched={touched.password}
                      type="password"
                    /> */}

                                        <CustomInput
                                            name="gouvernorat"
                                            label={i18n.t('address')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.gouvernorat}
                                            errors={errors.gouvernorat}
                                            touched={touched.gouvernorat}
                                        />
                                        <CustomInput
                                            name="city"
                                            label={i18n.t('governorate')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.city}
                                            errors={errors.city}
                                            touched={touched.city}
                                        />

                                    </Col>
                                    <Col md={6}>







                                        <label htmlFor="basic-url">{i18n.t('agency')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="agence"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.agence}
                                            >
                                                <option>{i18n.t('choose-agency')}</option>

                                                {this.state.agences.map((agence, index) => (
                                                    <option key={agence.id} value={agence.id}>
                                                        {agence.name}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>



                                        {/* <CustomInput
                      name="company"
                      label="company"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.company}
                      errors={errors.company}
                      touched={touched.company}
                    /> */}
                                        {/* <CustomInput
                      name="codeFiscal"
                      label="codeFiscal"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.codeFiscal}
                      errors={errors.codeFiscal}
                      touched={touched.codeFiscal}
                    /> */}

                                    </Col>
                                </Row>

                                <Button
                                    onClick={() => {
                                        this.handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };

    render() {
        return (
            <Formik
                initialValues={{
                    agence: "",
                    city: "",

                    gouvernorat: "",
                }}
                onSubmit={(values, { setSubmitting }) => {
                    this.submitForm(values);
                    setSubmitting(false);
                }}
                validationSchema={SignupSchema}
            >
                {(props) => this.showForm(props)}
            </Formik>
        );
    }
}

export default AddZone;
